import React from 'react';
import Calendar from 'react-calendar';
import OverlayTrigger from 'react-bootstrap/OverlayTrigger';
import Tooltip from 'react-bootstrap/Tooltip';
import 'react-calendar/dist/Calendar.css';
import Container from 'react-bootstrap/Container';

export default function BookingCalendar () {
    const isBookable =(date) => {
        const today = new Date();
        const isPast = date < today;

        return !isPast && (date.getDay() === 2 ||
        date.getDay() === 4 ||
        (date.getDay() === 5 && date.getDate() % 2 === 1) ||
        (date.getDay() === 6 && date.getDate() % 2 === 1));
    }

    const tileClassName = ({ date }) => {
        if (isBookable(date)) {
            return 'bookable text-success bg-success-subtle border border-success-subtle';
        }
        else {
            return '';
        }
    };

    const tileDisabled = ({ date }) => {
        return date.getDay() === 0;
    };

    const renderTooltip = (props) => (
        <Tooltip id="button-tooltip" {...props}>
          Simple tooltip
        </Tooltip>
      );

    const Link = ({ id, children, title }) => (
        <OverlayTrigger overlay={<Tooltip id={id}>{title}</Tooltip>}>
            <a href="#">{children}</a>
        </OverlayTrigger>
    );

    const tileContent = ({ activeStartDate, date, view }) => {
        return view === 'month' && date.getDay() === 0 
        ? <Link title="Default title" id="t-1"></Link>
        : null
    };

    return (
        <Calendar tileClassName={tileClassName}/>
    );
};
